.landingpage{
  background-color: white;
  display: flex;
  height: 100vh;
  justify-content: center;
}
.landingContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 30px;
  &__logo{
    width: 200px;
  }
  &__article{
    text-align: center;
    &--title{
      font-size: 18.2px;
      font-family: 'Roboto', sans-serif;
      font-weight: bold;
      margin-bottom: 10px;
    }
    &--paragraph{
      font-size: 16px;
    }
  }
}